import { VirtualizeSwipeableViews, slideRenderer } from "./list/CouseList";
import { useEffect } from "react";
import { getfetch } from "./common";
import useWindowDimensions from "../hook/useWindowDimensions";
import { CourseType } from "../types/Course";
import _ from "lodash";
import { useRecoilState } from "recoil";
import { CourseIndexState, CourseListState, CreateRouteBottomModalState, TagListState } from "./recoil";

export const tagInitList = ["", "", "", "", "", "", ""];

export const CourseArea = () => {
  const [courseList, setCourseList] = useRecoilState<CourseType[]>(CourseListState);
  const [tagList, setTagList] = useRecoilState<string[]>(TagListState);
  const [index, setIndex] = useRecoilState<number>(CourseIndexState);
  const { height, width } = useWindowDimensions();
  const [visibleRoute, setVisibleRoute] = useRecoilState(CreateRouteBottomModalState);

  //#region   
  useEffect(() => {
    getfetch("/courses").then(async (d: CourseType[]) => {
      setCourseList(d);
      //#region     ()
      //    ,   .
      let tagList: string[] = [];
      await d.map((course) => course.tags.map((tag) => tagList.push(tag.name)));
      tagList = await _.sortBy(_.groupBy(tagList), ["length"])
        .map((n) => n[0])
        .reverse()
        .filter((i) => i !== "");
      tagList.unshift(""); //   
      setTagList(tagList);
      //#endregion
    });
  }, [visibleRoute]);
  //#endregion
  if (tagList.length === 0) return false;
  return (
    <VirtualizeSwipeableViews
      index={index}
      onChangeIndex={setIndex}
      slideRenderer={(p: { index: number; key: number }) => slideRenderer(p, tagList, width)}
    />
  );
};
